"use client";
import { useState } from "react";

export default function WaitingLobby({
  players,
  roomId,
}: {
  players: any[];
  roomId: string;
}) {
  const [copied, setCopied] = useState(false);
  const inviteLink = `${window.location.origin}/?room=${roomId}`;

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(inviteLink);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Failed to copy:", err);
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center">
      <div className="bg-[var(--color-bg)] border border-[var(--color-line)] rounded-2xl shadow-lg w-[90%] max-w-md p-6 text-[var(--color-fg)]">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-semibold">Players in Room</h2>
          <button
            onClick={handleCopy}
            className="text-sm bg-[var(--color-cyan)] text-[var(--color-bg)] hover:bg-[var(--color-green)] transition-all font-medium px-3 py-1 rounded-md"
          >
            {copied ? "Copied!" : "Copy Invite Link"}
          </button>
        </div>

        <div className="border-t border-[var(--color-line)] pt-3">
          {players.length > 0 ? (
            <ul className="space-y-2">
              {players.map((val) => (
                <li
                  key={val.id}
                  className="bg-[var(--color-line)]/20 hover:bg-[var(--color-line)]/40 rounded-lg px-3 py-2 text-lg transition-colors"
                >
                  {val.username}
                </li>
              ))}
            </ul>
          ) : (
            <p className="text-[var(--color-comment)] text-center py-4">
              Waiting for players to join...
            </p>
          )}
        </div>

        <div className="mt-4 text-sm text-center text-[var(--color-comment)]">
          Share this link with your friends to join:
          <br />
          <a
            href={inviteLink}
            className="text-[var(--color-cyan)] hover:underline break-all"
          >
            {inviteLink}
          </a>
        </div>
      </div>
    </div>
  );
}
